import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '../../../../../../../node_modules/@angular/material';
import { ItemMaster } from '../item-master';
import { EditCustomerComponent } from '../../../CRM/customer/edit-customer/edit-customer.component';
import { ItemMasterService } from '../item-master.service';
@Component({
  selector: 'app-edit-item-master',
  templateUrl: './edit-item-master.component.html',
  styleUrls: ['./edit-item-master.component.css']
})
export class EditItemMasterComponent implements OnInit {

  constructor(private _itemMasterService: ItemMasterService,
    @Inject(MAT_DIALOG_DATA) public data: ItemMaster,
    public dialogRef: MatDialogRef<EditCustomerComponent>) { }

  ngOnInit() {
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  editItemMaster() {
    this._itemMasterService.editItemMaster(this.data).subscribe((ItemMaster) => {
      alert("Edited Successfully");
      this.dialogRef.close();
    }, error => {
      alert("Don't Edit");
    });
  }

}




