import { Component, OnInit, ViewChild } from '@angular/core';
import { ItemMaster } from './item-master';
import { Router } from '@angular/router';
import { MatTableDataSource, MatDialog, MatSort, MatPaginator } from '@angular/material';
import { ItemMasterService } from './item-master.service';
import { AddItemMasterComponent } from './add-item-master/add-item-master.component';
import { DeleteItemMasterComponent } from './delete-item-master/delete-item-master.component';
import { EditItemMasterComponent } from './edit-item-master/edit-item-master.component';
@Component({
  selector: 'app-item-master',
  templateUrl: './item-master.component.html',
  styleUrls: ['./item-master.component.css']
})
export class ItemMasterComponent implements OnInit {
  private itemMaster: ItemMaster[];
  dataSource;
  index: number;
  itemId: number;
  displayedColumns = ['itemId', 'itemCode', 'itemName', 'itemGroupName', 'actions']


  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(private _itemMasterService: ItemMasterService,
    private _router: Router,
    private _dialog: MatDialog) { }


  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }


  ngOnInit() {
    this.getAllItemMaster();

  }


  getAllItemMaster() {
    this._itemMasterService.getAllItemMaster().subscribe((ItemMaster) => {
      this.dataSource = new MatTableDataSource(ItemMaster);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }, (error) => {
      console.log(error);
    }
    );
  }

  saveItemMaster(itemMaster: ItemMaster) {
    const dialogRef = this._dialog.open(AddItemMasterComponent, {
      data: { itemId: ItemMaster }
    });
    dialogRef.afterClosed().subscribe(result => {
        this.getAllItemMaster();
    });
  }


  deleteItemMaster(i: number, itemId: number, itemCode: string, itemName: string, itemGroupName: string) {
    this.index = i;
    this.itemId = itemId;
    const dialogRef = this._dialog.open(DeleteItemMasterComponent, {
      data: { itemId: itemId, itemCode: itemCode, itemName: itemName, itemGroupName: itemGroupName }
    });
    dialogRef.afterClosed().subscribe(result => {
        this.getAllItemMaster();
    })
  }


  editItemMaster(i: number, itemId: number, itemCode: string, itemName: string, itemGroupName: string) {
    this.itemId = itemId;
    const dialogRef = this._dialog.open(EditItemMasterComponent, {
      data: { itemId: itemId, itemCode: itemCode, itemName: itemName, itemGroupName: itemGroupName }
    });
    dialogRef.afterClosed().subscribe(result => {
        this.getAllItemMaster();
    });
  }
}



