import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router } from '@angular/router';
import { ItemMaster } from '../item-master';
import { ItemMasterService } from '../item-master.service';
import { ItemGroupMaster } from '../../item-group-master/item-group-master';
import { ItemGroupMasterService } from '../../item-group-master/item-group-master.service';
@Component({
  selector: 'app-add-item-master',
  templateUrl: './add-item-master.component.html',
  styleUrls: ['./add-item-master.component.css']
})
export class AddItemMasterComponent implements OnInit {

  private itemMaster: ItemMaster;
  private itemGroup: ItemGroupMaster[];

  constructor(private _itemMasterService: ItemMasterService,
    private _itemGroupMasterService: ItemGroupMasterService,
    public dialogRef: MatDialogRef<AddItemMasterComponent>,
    @Inject(MAT_DIALOG_DATA) private _router: Router) { }

  ngOnInit() {
    this.itemMaster = this._itemMasterService.getter();
    this.itemMaster = new ItemMaster();
    this.getItemGroup();
  }

  errorMessage: string;
  processForm() {
    this._itemMasterService.saveItemMaster(this.itemMaster).subscribe((itemMaster) => {
      alert("saved Successsfully");
      this.dialogRef.close();
    }, error => {
      alert("Don't Save Item Code Is Unique")
    });
  }

  getItemGroup() {
    this._itemGroupMasterService.getAllItemGroup().subscribe((data) => {
      this.itemGroup = data;
    }, (error) => {
      console.log(error);
    });
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
}
