import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '../../../../../../../node_modules/@angular/material';
import { ItemMasterService } from '../item-master.service';

@Component({
  selector: 'app-delete-item-master',
  templateUrl: './delete-item-master.component.html',
  styleUrls: ['./delete-item-master.component.css']
})
export class DeleteItemMasterComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<DeleteItemMasterComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public _itemMasterService: ItemMasterService) { }

  ngOnInit() {
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
  confirmDeleteItemMaster() {
    this._itemMasterService.deleteItemMaster(this.data.itemId).subscribe((itemMaster) => {
      alert("Deleted Successfully");
      this.dialogRef.close();
    }, error => {
      alert("Does't Delete!");
    });
  }
}






